  ColonelKurtz.prototype.$ColonelKurtz_getDomElement=function()          {"use strict";
    return this.el
  };

  ColonelKurtz.prototype.$ColonelKurtz_runCallbacks=function()       {"use strict";
    var json = this.toJSON()

    this.$ColonelKurtz_callbacks.forEach(function(callback){
      callback(json)
    })
  };

  ColonelKurtz.prototype.$ColonelKurtz_rootBlockListId=function()         {"use strict";
    var rootBlockList = this.rootBlockList()

    if (rootBlockList) {
      return rootBlockList.id
    }
  };



ColonelKurtz.addBlockType = function(id        , component     ) {
  if (React.isValidElement(component) === false) {
    component = ColonelKurtz.createBlock(component)
  }

  BlockTypeActions.create({ id:id, component:component })
}

ColonelKurtz.createBlock = function(blockClass) {
  var mixins = blockClass.mixins || []

  mixins = mixins.concat(BlockTypeMixin)

  return React.createClass(
    Object.assign({}, blockClass, { React:React, mixins:mixins })
  )
}

ColonelKurtz.addons = require('./addons')

module.exports = ColonelKurtz



