/* @flow */

var BlockConstants = require('../constants/block_constants')
var Dispatcher = require('../dispatcher')

var BlockActions = {

  create:function(params                                                                    ) {
    Dispatcher.dispatch(Object.assign({ type: BlockConstants.BLOCK_CREATE}, params ))
  },

  destroy:function(params                                                ) {
    Dispatcher.dispatch(Object.assign({ type: BlockConstants.BLOCK_DESTROY}, params ))
  },

  update:function(blockId        , content        ) {
    Dispatcher.dispatch({ type: BlockConstants.BLOCK_UPDATE, blockId:blockId, content:content })
  }

}
module.exports = BlockActions



