/* @flow */

var Modes          = require('../constants/mode_constants')
var ContentSection = require('./content_section')
var ModeSelection  = require('./mode_selection')
var React          = require('react')

var App = React.createClass({displayName: 'App',

  getInitialState:function() {
    return {
      mode: Modes.EDIT_MODE
    }
  },

  getBlockListId:function()         {
    return this.props.editor.rootBlockList().id
  },

  setMode:function(mode        )           {
    return function()  {return this.setState({ mode: mode });}.bind(this)
  },

  render:function()      {
    return (
      React.createElement("div", {className: "colonel"}, 
        React.createElement(ModeSelection, {mode:  this.state.mode, setMode:  this.setMode}), 
        React.createElement(ContentSection, {mode:  this.state.mode, initialBlockListId:  this.getBlockListId() })
      )
    )
  }

})

module.exports = App



