/* @flow */

var Bus        = require('../bus')
var Block      = require('../models/block')
var Constants  = require('../constants/block_constants')
var Dispatcher = require('../dispatcher')
var Immutable  = require('immutable')

var _blocks = Immutable.List()

var BlockStore = {

  all:function()               {
    return _blocks
  },

  last:function() {
    return _blocks.last()
  },

  find:function(id        )        {
    return _blocks.find(function(block)  {return block.id === id;} )
  },

  _create:function(parentBlockListId        , type        )        {
    var block = new Block({ parentBlockListId:parentBlockListId, type:type })

    _blocks = _blocks.push(block)

    Bus.publish()

    return block
  },

  _destroy:function(id        ) {
    _blocks = _blocks.filter(function(b)  {return b.id !== id;})
    Bus.publish()
  },

  _update:function(blockId        , content        ) {
    var block = BlockStore.find(blockId)

    if (block) {
      // This could probably be done more immutably, but seems fine as is.
      block.content = content
      Bus.publish()
    }
  },

  dispatchToken: Dispatcher.register(function(action) {
    switch (action.type) {
      case Constants.BLOCK_CREATE:
        var block = BlockStore._create(action.parentBlockListId, action.blockType)
        action.block = block
        break
      case Constants.BLOCK_DESTROY:
        BlockStore._destroy(action.blockId)
        break
      case Constants.BLOCK_UPDATE:
        BlockStore._update(action.blockId, action.content)
        break
      default:
        // do nothing
    }
  })

}

module.exports = BlockStore



